export default {
	data() {
		return {
			deinfos: [{
					id: 1,
					title: "广州市牛哄哄修车旗舰店",
					src: "",
					text: ""
				},
				{
					id: 2,
					title: "联系方式",
					src: "../../static/icon/colorphone.png",
					text: "202-88888888888"
				},
				{
					id: 3,
					title: "店铺地址",
					src: "../../static/icon/addrs.png",
					text: "广州市天河区中山大道中66号某某路左侧对面"
				},
				{
					id: 4,
					title: "服务期限",
					src: "../../static/icon/sertime.png",
					text: "2020-01-03 至 2022-01-04"
				}
			],
			starlist: [{
					id: 1,
					opartion: false
				},
				{
					id: 2,
					opartion: false
				},
				{
					id: 3,
					opartion: false
				},
				{
					id: 4,
					opartion: false
				},
				{
					id: 5,
					opartion: false
				}
			],
			starnum: 3 ,//店铺的评价星星数
			id:'',
			sercon:'',
			starss:'',//传给后台的星星数
			ImgList:[],
			conts:'',//传给后台的评价内容
			subDuo:true
			
		}
	},
	methods:{
		jum(e){
			uni.navigateTo({
				url:e
			})
		},
		
		addImg(){
			var self = this;
			// if(self.ImgList.length >= 3){
			// 	uni.showToast({
			// 		title:'萨瓦迪卡,最多可以上传三张照片哈!',
			// 		icon:'none'
			// 	});
			// 	return
			// }
				uni.chooseImage({
				    success: (chooseImageRes) => {
				        const tempFilePaths = chooseImageRes.tempFilePaths;
				        uni.uploadFile({
				            url: self.$service.api.user.updateIdImage, //仅为示例，非真实的接口地址
							header: {
								Authorization: 'Bearer ' + uni.getStorageSync('token')
							},
				            filePath: tempFilePaths[0],
				            name: 'file',
				            success: (res) => {
				                console.log(JSON.parse(res.data),"res");
								const path = JSON.parse(res.data);//打印出来就知道为什么要用JSON.parse
								const urls = path.data.url;
								self.ImgList.push(urls);
								console.log(self.ImgList,"ImgList");
								uni.showToast({
									title:path.info,
									icon:'none'
								});
				            }
				        });
				    }
				});
		},
		
		close(i){
			this.ImgList.splice(i , 1);
			console.log("success" , this.ImgList)
		},
		
		stars(e){
			// var nums = this.starnum;
			this.starss = e+1;
			console.log(this.starss);
			for (let i = 0; i <= e; i++) { //前面opartion的属性改成true店铺的评价星星数
				this.$set(this.starlist[i], "opartion", true);
				// this.starlist[i].opartion = !this.starlist[i].opartion;
			}
			for (let i = e+1; i <= 4; i++) { //后面opartion的属性改成店铺的评价星星数
				this.$set(this.starlist[i], "opartion", false);
				// this.starlist[i].opartion = !this.starlist[i].opartion;
			}
		},
		submit(){
			if(this.conts == ''){
				uni.showToast({
					title:'请输入评价内容',
					icon:'none'
				});
				return
			}
			if(this.starss == ''){
				uni.showToast({
					title:'请选择星星数',
					icon:'none'
				});
				return
			}
			if(this.subDuo){
			this.$service.auth(this , this.$service.api.index.comment , 'POST' , {
				merch_id:this.id,
				star:this.starss,
				content:this.conts,
				images:this.ImgList
			},function(self , res){
				self.subDuo = false;
				console.log(res);
				uni.showToast({
					title:res.info,
					icon:'none'
				});
				setTimeout(()=>{
					uni.navigateTo({
						url:"/pages/index/storeinfo"
					})
				},1500);
			});
			}else{
				uni.showToast({
					title:"请勿多次提交!",
					icon:"none"
				});
			}
		}
		
	},
	onLoad(op) {
		this.id = op.id;
	},
	onShow() {
		// var self = this;
		this.$service.auth(this , this.$service.api.main.merch_list ,'POST' , {
			// page:1,
			// limit:15,
			id:this.id
			// type:1,
			// lat:this.longitude,
			// lng:this.latitude,
			// rate:2,
			// distance:2
		},function(self , res){
			console.log(res);
			self.sercon = res.data.data[0];
		});


	}
}
